body{
  background: black;
}
.modal{
  width:500px;
  height:350px;
  left:50%;
  margin-top:10%;
  border-radius: 10px;
  position:fixed;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-shadow: 1px 1px 16px 5px rgba(0,0,0,0.1);
          box-shadow: 1px 1px 16px 5px rgba(0,0,0,0.1);
  background:white;

}

.modal-header{
 border-top-left-radius: 10px;
 border-top-right-radius: 10px;
 background-color: tomato;
 color:white;
 display:-ms-flexbox;
 display:flex;
 -ms-flex-align: center;
     align-items: center;
 -ms-flex-pack: center;
     justify-content: center;
 font-size: 3em;
 font-weight: 900;
 padding:0.5em;
}

.modal-body{
  padding:1em;
  font-size: 2em;
}

.before{

  -webkit-transform: translate(-50%, -150%);

      -ms-transform: translate(-50%, -150%);

          transform: translate(-50%, -150%);
  -webkit-transition: -webkit-transform 0.1s linear;
  transition: -webkit-transform 0.1s linear;
  -o-transition: transform 0.1s linear;
  transition: transform 0.1s linear;
  transition: transform 0.1s linear, -webkit-transform 0.1s linear;
}

.after{
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.0, 3, 0.0, -0.5);
  transition: -webkit-transform 0.3s cubic-bezier(0.0, 3, 0.0, -0.5);
  -o-transition: transform 0.3s cubic-bezier(0.0, 3, 0.0, -0.5);
  transition: transform 0.3s cubic-bezier(0.0, 3, 0.0, -0.5);
  transition: transform 0.3s cubic-bezier(0.0, 3, 0.0, -0.5), -webkit-transform 0.3s cubic-bezier(0.0, 3, 0.0, -0.5);
}

.bye{
  display:none;
}



